import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Col, Row, Space, Button, Table, Image, Drawer, Form, Select, InputNumber, Input, Switch, Typography, Collapse } from 'antd'
import toast from 'react-hot-toast'
import AlertWait from '../../pages/AlertWait'
import { getCatalogos, getDatosUsuario } from '../../utils/Utils'
import * as anticons from '@ant-design/icons'

import PostulantService from '../../service/PostulantService'
import BeneficiaryService from './../../service/BeneficiaryService'

const postulantService = new PostulantService()
const beneficiaryService = new BeneficiaryService()

const { Option } = Select
const { Title } = Typography

const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
        position: ['none', 'bottomRight']
    }
}
const panelStyle = {
    marginBottom: 20,
    backgroundColor: '#fafafa',
    border: 'none',
    borderRadius: '15px'
}

const Beneficiaries = () => {
    const navigate = useHistory()
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isLoadingBeneficiaries, setIsLoadingBeneficiaries] = useState(false)
    const [isSaving, setIsSaving] = useState(false)
    const [isDeleting, setIsDeleting] = useState(false)
    const [postulant, setPostulant] = useState({})
    const [beneficiaryC1, setBeneficiaryC1] = useState(null)
    const [beneficiaryC2, setBeneficiaryC2] = useState(null)
    const [openC1, setOpenC1] = useState(false)
    const [openC2, setOpenC2] = useState(false)
    const [formBeneficiaryC1] = Form.useForm()
    const [formBeneficiaryC2] = Form.useForm()

    const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
        <>
            <Image
                alt={e.catNombre}
                preview={false}
                src={`https://flagcdn.com/24x18/${e.catDescripcion.substr(0, 2).toLowerCase()}.png`}
                srcSet={`https://flagcdn.com/48x36/${e.catDescripcion.substr(0, 2).toLowerCase()}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion.substr(0, 2).toLowerCase()}.png 3x`}
                width="24"
                height="18"
            ></Image>
            &nbsp;{
                e.catNombre
            }
        </>
    </Option>

    const renderCourse = ({ curId, curNombre }, posId) =>
        <Collapse.Panel
            key={curId}
            header={
                <Row gutter={10}>
                    <Col>
                        <Typography.Text strong style={{ fontSize: '1.1em' }}>
                            {curNombre.catNombre}
                        </Typography.Text>
                    </Col>
                </Row>
            }
            style={panelStyle}
        >
            <Row>
                <Col span={24}
                    style={{ textAlign: 'right' }}>
                    <Space>
                        <Button
                            type='default'
                            loading={isLoading || isLoadingBeneficiaries}
                            disabled={isLoading || isLoadingBeneficiaries}
                            style={{ width: '130px' }}
                            onClick={() => {
                                fetchData(2)
                            }}>Refresh</Button>
                        <Button
                            type='primary'
                            disabled={isLoading || isLoadingBeneficiaries}
                            style={{ width: '130px' }}
                            onClick={() => {
                                if (curNombre.catValor === 'CUR_NEG_VER1') {
                                    closeBeneficiaryC1()
                                    openBeneficiaryC1({
                                        idCurso: curId,
                                        idPostulante: posId,
                                        benTrabaja: false,
                                        idTipo: getCatalogos().typePersons.filter(t => t.catValor === 'TYP_PER_NAT')[0].catId
                                    })
                                } else if (curNombre.catValor === 'CUR_NEG_VER2') {
                                    closeBeneficiaryC2()
                                    openBeneficiaryC2({
                                        idCurso: curId,
                                        idPostulante: posId,
                                        idTipo: getCatalogos().typePersons.filter(t => t.catValor === 'TYP_PER_LEG')[0].catId
                                    })
                                }
                            }}>New Beneficiary</Button>
                    </Space>
                </Col>
            </Row>
            {
                postulant && postulant.beneficiarioList && <Table
                    loading={isLoading || isLoadingBeneficiaries}
                    {...tableProps}
                    rowKey='benId'
                    columns={[
                        {
                            title: '',
                            dataIndex: 'benId',
                            render: (_, { benId }) => <Image key={benId} preview={false} alt='' src='./assets/user.png' width={32} height={32} />
                        },
                        {
                            title: 'ID Number',
                            dataIndex: 'benDocumento',
                            render: (_, { benDocumento }) => `${benDocumento}`
                        },
                        {
                            title: 'Name',
                            dataIndex: 'benNombres',
                            render: (_, { benNombres }) => `${benNombres}`
                        },
                        {
                            title: 'Type Person',
                            dataIndex: 'benTipo',
                            render: (_, { benTipo }) => `${benTipo.catNombre}`
                        },
                        {
                            title: 'Country',
                            dataIndex: 'benPais',
                            render: (_, { benPais }) => `${benPais.catNombre}`,
                            responsive: ['md', 'lg', 'xl', 'xxl']
                        },
                        {
                            title: 'Actions',
                            dataIndex: '',
                            render: (_, record) => <div style={{ textAlign: 'center' }}>
                                <Button type='primary' onClick={() => {
                                    if (curNombre.catValor === 'CUR_NEG_VER1') {
                                        getBeneficiaryC1(record)
                                    } else if (curNombre.catValor === 'CUR_NEG_VER2') {
                                        getBeneficiaryC2(record)
                                    }
                                }}>View</Button>
                            </div>
                        }
                    ]}
                    dataSource={postulant.beneficiarioList.filter(b => b.idCurso === curId)}>
                </Table>
            }
        </Collapse.Panel>

    const openBeneficiaryC1 = (values) => {
        setBeneficiaryC1(values)
        setOpenC1(true)
    }
    const closeBeneficiaryC1 = () => {
        formBeneficiaryC1.resetFields()
        setOpenC1(false)
    }
    const getBeneficiaryC1 = async ({ benId }) => {
        setIsLoadingBeneficiaries(true)
        try {
            const { data } = await beneficiaryService.get({ navigate, beneficiary: { benId } })
            const beneficiary = data.listObject[0]
            setOpenC1(true)
            setBeneficiaryC1(beneficiary)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsLoadingBeneficiaries(false)
        }
    }
    const saveBeneficiaryC1 = async (values) => {
        setIsSaving(true)
        const beneficiary = { ...values }
        try {
            const { data } = await beneficiaryService.put({ navigate, beneficiary })
            if (data) fetchData(2)
            closeBeneficiaryC1()
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsSaving(false)
        }
    }

    const openBeneficiaryC2 = (values) => {
        setBeneficiaryC2(values)
        setOpenC2(true)
    }
    const closeBeneficiaryC2 = () => {
        formBeneficiaryC2.resetFields()
        setOpenC2(false)
    }
    const getBeneficiaryC2 = async ({ benId }) => {
        setIsLoadingBeneficiaries(true)
        try {
            const { data } = await beneficiaryService.get({ navigate, beneficiary: { benId } })
            const beneficiary = data.listObject[0]
            setOpenC2(true)
            setBeneficiaryC2(beneficiary)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsLoadingBeneficiaries(false)
        }
    }
    const saveBeneficiaryC2 = async (values) => {
        setIsSaving(true)
        const beneficiary = { ...values }
        try {
            const { data } = await beneficiaryService.put({ navigate, beneficiary })
            if (data) fetchData(2)
            closeBeneficiaryC2()
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsSaving(false)
        }
    }

    const fetchData = async (typeLoad) => {
        setLoad(false)
        if (typeLoad) {
            if (typeLoad === 1) {
                setIsLoading(true)
            } else if (typeLoad === 2) {
                setIsLoadingBeneficiaries(true)
            }
        } else {
            setIsLoading(true)
        }
        try {
            const postulant = { idUsuario: getDatosUsuario().usuarioId }
            const { data } = await postulantService.getTraining({ navigate, postulant })
            const training = data.listObject[0]
            setPostulant(training)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        } finally {
            if (typeLoad) {
                if (typeLoad === 1) {
                    setIsLoading(false)
                } else if (typeLoad === 2) {
                    setIsLoadingBeneficiaries(false)
                }
            } else {
                setIsLoading(false)
            }
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    return (
        <>
            <div style={
                {
                    backgroundColor: '#ffffff',
                    padding: '15px 20px 6px 20px',
                    borderRadius: '15px',
                    marginBottom: '20px'
                }
            }>
                <Row>
                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                        <Typography>
                            <Title type='secondary' level={3}>
                                Beneficiaries
                            </Title>
                        </Typography>
                    </Col>
                </Row>
            </div>
            <Row justify='center'>
                {
                    isLoading ?
                        <AlertWait message='Loading...' isLoading={isLoading} />
                        :
                        <Col span={24}>
                            <Collapse
                                accordion
                                bordered={false}
                                expandIconPosition='end'
                                style={{
                                    backgroundColor: 'transparent'
                                }}
                            >
                                {
                                    postulant.entId && postulant.entId.cursoList && postulant.entId.cursoList.map(c => renderCourse(c, postulant.posId))
                                }
                            </Collapse>
                        </Col>
                }
            </Row>
            <Drawer title="Beneficiary" placement="right" onClose={closeBeneficiaryC1} open={openC1}
                extra={
                    <Space>
                        <Button onClick={closeBeneficiaryC1}
                            disabled={isSaving}
                        >Cancel</Button>
                        <Button type="primary"
                            disabled={isSaving}
                            loading={isSaving}
                            onClick={() => {
                                formBeneficiaryC1.submit()
                            }}>
                            Save
                        </Button>
                    </Space>
                }
            >
                <Form
                    form={formBeneficiaryC1}
                    layout={'vertical'}
                    initialValues={beneficiaryC1}
                    onFinish={saveBeneficiaryC1}
                    disabled={isSaving}
                >
                    <div style={{ display: 'none' }}>
                        <Form.Item
                            name={['idTipo']}
                            label='Type Person'
                            rules={[
                                {
                                    required: true
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name={['idCurso']}
                            label='Course'
                            rules={[
                                {
                                    required: true
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name={['idPostulante']}
                            label='Postulante'
                            rules={[
                                {
                                    required: true
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </div>
                    <Form.Item
                        name={['benDocumento']}
                        label='ID Number'
                        rules={[
                            {
                                required: true
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['benNombres']}
                        label='Name'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['idPais']}
                        label="Country"
                        rules={[
                            {
                                required: true
                            },
                        ]}
                    >
                        <Select name='idPais'>
                            {
                                getCatalogos().paises.map(p => renderPaisValor(p))
                            }
                        </Select>
                    </Form.Item>
                    <Form.Item
                        name={['ciudad']}
                        label='City'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['idSexo']}
                        label='Sex'
                        rules={[
                            {
                                required: true,
                            },
                        ]}
                    >
                        <Select
                            style={{
                                width: '100%',
                                display: 'inline-block'
                            }}
                            fieldNames={{
                                label: 'catNombre',
                                value: 'catId'
                            }}
                            showSearch
                            options={getCatalogos().generos}
                            optionFilterProp="catNombre"
                        />
                    </Form.Item>
                    <Form.Item
                        name='benTrabaja'
                        label='Are you currently working?'
                        valuePropName='checked'
                    >
                        <Switch />
                    </Form.Item>
                    <Form.Item
                        name={['idNivelInt']}
                        label='Level of instruction'
                        rules={[
                            {
                                required: true,
                            },
                        ]}
                    >
                        <Select
                            style={{
                                width: '100%',
                                display: 'inline-block'
                            }}
                            fieldNames={{
                                label: 'catNombre',
                                value: 'catId'
                            }}
                            showSearch
                            options={getCatalogos().educationLevels}
                            optionFilterProp="catNombre"
                        />
                    </Form.Item>
                </Form>
            </Drawer>
            <Drawer title="Beneficiary" placement="right" onClose={closeBeneficiaryC2} open={openC2}
                extra={
                    <Space>
                        <Button onClick={closeBeneficiaryC2}
                            disabled={isSaving}
                        >Cancel</Button>
                        <Button type="primary"
                            disabled={isSaving}
                            loading={isSaving}
                            onClick={() => {
                                formBeneficiaryC2.submit()
                            }}>
                            Save
                        </Button>
                    </Space>
                }
            >
                <Form
                    form={formBeneficiaryC2}
                    layout={'vertical'}
                    initialValues={beneficiaryC2}
                    onFinish={saveBeneficiaryC2}
                    disabled={isSaving}
                >
                    <div style={{ display: 'none' }}>
                        <Form.Item
                            name={['idTipo']}
                            label='Type Person'
                            rules={[
                                {
                                    required: true
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name={['idCurso']}
                            label='Course'
                            rules={[
                                {
                                    required: true
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name={['idPostulante']}
                            label='Postulante'
                            rules={[
                                {
                                    required: true
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </div>
                    <Form.Item
                        name={['benDocumento']}
                        label='ID Number'
                        rules={[
                            {
                                required: true
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['benNombres']}
                        label='Name'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['idPais']}
                        label="Country"
                        rules={[
                            {
                                required: true
                            },
                        ]}
                    >
                        <Select name='idPais'>
                            {
                                getCatalogos().paises.map(p => renderPaisValor(p))
                            }
                        </Select>
                    </Form.Item>
                    <Form.Item
                        name={['ciudad']}
                        label='City'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['idSexo']}
                        label='Sex'
                        rules={[
                            {
                                required: true,
                            },
                        ]}
                    >
                        <Select
                            style={{
                                width: '100%',
                                display: 'inline-block'
                            }}
                            fieldNames={{
                                label: 'catNombre',
                                value: 'catId'
                            }}
                            showSearch
                            options={getCatalogos().generos}
                            optionFilterProp="catNombre"
                        />
                    </Form.Item>
                    <Form.Item
                        name={['benIdEmpresa']}
                        label='Company ID'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['benNomEmpresa']}
                        label='Company Name'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['benCargo']}
                        label='Job'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name={['benNumPer']}
                        label='Number of people'
                        rules={[
                            {
                                required: true,
                            }
                        ]}
                    >
                        <InputNumber
                            style={{
                                width: '100%',
                                display: 'inline-block'
                            }}
                        />
                    </Form.Item>
                    <Row gutter={[10]}>
                        <Col span={12}>
                            <Form.Item
                                name={['benHombres']}
                                label='Men'
                                rules={[
                                    {
                                        required: true,
                                    }
                                ]}
                            >
                                <InputNumber
                                    style={{
                                        width: '100%',
                                        display: 'inline-block'
                                    }}
                                />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item
                                name={['benMujeres']}
                                label='Women'
                                rules={[
                                    {
                                        required: true,
                                    }
                                ]}
                            >
                                <InputNumber
                                    style={{
                                        width: '100%',
                                        display: 'inline-block'
                                    }}
                                />
                            </Form.Item>
                        </Col>
                    </Row>
                </Form>
            </Drawer>
        </>
    )
}

export default Beneficiaries